<template>
  <div class="news-box">
    <div class="news-bar">
      <van-nav-bar title="期货资讯" left-arrow @click-left="linkHome"/>
    </div>
    <div class="information" ref="information">
      <div class="information-content">
        <div>
          <div class="information-img"></div>
          <div class="information-title">大商所苯乙烯期货首秀：主力合约涨1.56%</div>
          <div class="information-source">来源：东方财富研究中心</div>
          <div class="information-time">9.27</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
export default {
  name: 'news',
  mounted () {
    this.$nextTick(() => {
      // $refs绑定元素
      if (!this.scroll) {
        this.scroll = new BScroll(this.$refs.information, {
          // 开启点击事件 默认为false
          click: true
        })
        // console.log(this.scroll)
      } else if (!this.$refs.information) {

      } else {
        this.scroll.refresh()
      }
    })
  },
  methods: {
    linkHome () {
      this.$router.go(-1)
    }
  }
}
</script>

<style scoped lang="scss">
  @import "../../style/news";
</style>



